				/**/
				todos3.forEach(function(el) {
					el.select = false;
				});
//				todos3[index].select = true;
				if (this.seen == false) {
					todos3[index].select = true;
					this.seen = true;
				} else{
					todos3[index].select = false;
					this.seen = false;
				}
			}
