		/**;11;
		 * @param val
		 * @returns
		 */
		function isPhoneNumber(val){
			var test = new RegExp(/^[1][345789][0-9]{9}$/);
			return test.test(val);
		}

		/**
		 * <br>
		 * "";
		 * 
		 * @param val
		 *            object
		 * @returns <b>true</b>"";<br>
		 *          <b>false</b>"";
		 */
		function isEmpty(val) {
			var obj = val.replace(/ /g, "");
			return obj.length > 0 && obj != "" ? false : true;
		}

		/**;6~16;;
		 * @param val
		 * @returns
		 */
		function isPassword(val){
			var test = new RegExp(/^\w{6,16}$/);
			return test.test(val);
		}
		
		/**
		 * ; :121.1212.00.1222
		 * 
		 * @param val
		 *            object
		 * @returns <b>true</b>;<br>
		 *          <b>false</b>;
		 */
		function isPrice(val) {
			var test = new RegExp(/^[0-9]+(\.[0-9]+)?$/);
			return test.test(val);
		}
		
		/**
		 * 09;
		 * 
		 * @param val
		 *            object
		 * @returns <b>true</b>;<br>
		 *          <b>false</b>;
		 */
		function isOnlyNumber(val) {
			var test = new RegExp(/^[0-9]+$/);
			return test.test(val);
		}
		
		/**
		 * 
		 */
		function checkBankNum(str){
			if(str=="" || !isOnlyNumber(str) || (str.length <16  || str.length>19)){
				return false;
			}else {
				return true;
			}
		}


		/**
		 * @param len 
		 * @returns {String}
		 */
		function randomString(len) {
			len = len || 32;
			//alert(len);
			//console.log(len);
			var $chars = 'ABCDEFGHJKMNPQRSTWXYZabcdefhijkmnprstwxyz2345678';
			/** **oOLl,9gq,Vv,Uu,I1*** */
			var maxPos = $chars.length;
			var pwd = '';
			for (i = 0; i < len; i++) {
				pwd += $chars.charAt(Math.floor(Math.random() * maxPos));
			}
			return pwd;
		}

		
