		/** yyyy-MM-dd
		 * @param time time;
		 * @returns {String} yyyy-MM-dd
		 */
		function format(time){ 
			var myDate = new Date(time);
			var year = myDate.getFullYear();//(4,1970-????)
			var money = myDate.getMonth()+1+100;  //(0-11,01)
			var date = myDate.getDate()+100;        //(1-31)
			money = money.toString().substring(1);
			date = date.toString().substring(1);
			return year+"-"+money+"-"+date; 
		} 
		

		/** yyyy-MM-dd
		 * @param time time;
		 * @returns {String} yyyy-MM-dd
		 */
		function formatMMdd(time){ 
			var myDate = new Date(time);
			var year = myDate.getFullYear();//(4,1970-????)
			var money = myDate.getMonth()+1+100;  //(0-11,01)
			var date = myDate.getDate()+100;        //(1-31)
			money = money.toString().substring(1);
			date = date.toString().substring(1);
			return money+"-"+date; 
		}

		function fmoney(s)   
		{   var n = 0;
		   n = n >= 0 && n <= 20 ? n : 2;   
		   s = parseFloat((s + "").replace(/[^\d\.-]/g, "")).toFixed(n) + "";   
		   var l = s.split(".")[0].split("").reverse();//,   
		  // r = s.split(".")[1];   
		  var t = "";   
		   for(var i = 0; i < l.length; i ++ )   
		   {   
		      t += l[i] + ((i + 1) % 3 == 0 && (i + 1) != l.length ? "," : "");   
		   }   
		   return t.split("").reverse().join("");// + "." + r;   
		}


		/**;,  1820,2;
		 * @param sDate1 ;
		 * @param sDate2 ;
		 */
		function  DateDiff(sDate1, sDate2){    //sDate1sDate22006-12-18  
		    var  aDate,  oDate1,  oDate2,  iDays;  
		    aDate  =  sDate1.split("-");  
		    oDate1  =  new  Date(aDate[1]  +  '-'  +  aDate[2]  +  '-'  +  aDate[0]);    //12-18-2006  
		    aDate  =  sDate2.split("-");  
		    oDate2  =  new  Date(aDate[1]  +  '-'  +  aDate[2]  +  '-'  +  aDate[0]);  
		    iDays  =  parseInt(Math.abs(oDate1  -  oDate2)  /  1000  /  60  /  60  /24);    //  
		    return  iDays;  
		}




