<template>
	<div class="art-list">
		<div class="art-box" v-for="(todo, index) in todos" :class="[todo.select ? activeClass : '']" v-on:click="tab1(todos,index)">
			<div class="art-title">{{todo.text}}</div>
			<div v-if="seen" v-html="todo.text1" class="art-answ">{{todo.text1}}</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'hello',
		data: function() {
			return {
				activeClass: 'select',
				seen: false,
				todos: [{
						text: '',
						text1: '',
						select: false
					},
					{
						text: '',
						text1: '616',
						select: false
					},
					{
						text: '',
						text1: '',
						select: false
					},
					{
						text: '',
						text1: '',
						select: false
					},
					{
						text: '',
						text1: ',',
						select: false
					},
					{
						text: 'x',
						text1: 'X',
						select: false
					},
					{
						text: '',
						text1: '',
						select: false
					},
					{
						text: '',
						text1: '1   <br/>2<br/> 3<br/>',
						select: false
					},
					{
						text: '',
						text1: '',
						select: false
					},
					{
						text: '',
						text1: '',
						select: false
					},
					
					{
						text: '',
						text1: '<br/>1<br/>2<br/>3',
						select: false
					},
					{
						text: '',
						text1: '',
						select: false
					},
					{
						text: '',
						text1: 'APP',
						select: false
					},
					{
						text: '',
						text1: '',
						select: false
					},
					{
						text: '',
						text1: '',
						select: false
					}
				]
			}
		},
		methods: {
			tab1: function(todos, index) {
				/**/
				todos.forEach(function(el) {
					el.select = false;
				});
//				todos[index].select = true;
				if (this.seen == false) {
					todos[index].select = true;
					this.seen = true;
				} else{
					todos[index].select = false;
					this.seen = false;
				}
			}
		}
	}
</script>

<style>
	@import '../assets/css/help_center.css';
</style>


