<template>
	<div class="art-list">
		<div class="art-box" v-for="(todo2, index) in todos2" :class="[todo2.select ? activeClass : '']" v-on:click="tab3(todos2,index)">
			<div class="art-title">{{todo2.text}}</div>
			<div v-if="seen" v-html="todo2.text1" class="art-answ">{{todo2.text1}}</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'hello',
		data: function() {
			return {
				activeClass: 'select',
				seen: false,
				todos2: [
				{
						text: '',
						text1: '',
						select: false
					},
					{
						text: '',
						text1: '100',
						select: false
					},
				{
						text: '',
						text1: '',
						select: false
					},
					{
						text: '',
						text1: '',
						select: false
					},
					{
						text: '',
						text1: '1-7',
						select: false
					},
					{
						text: '',
						text1: '11<br/>2<br/>30<br/>4',
						select: false
					},
					
			
					{
						text: '',
						text1: '',
						select: false
					},
					{
						text: '',
						text1: '17:3017:30',
						select: false
					},
					{
						text: '',
						text1: '',
						select: false
					},
					{
						text: '',
						text1: '  ',
						select: false
					},
					{
						text: '',
						text1: '  ',
						select: false
					}
				]
			}
		},
		methods: {
			tab3: function(todos2, index) {
				/**/
				todos2.forEach(function(el) {
					el.select = false;
				});
//				todos2[index].select = true;
				if (this.seen == false) {
					todos2[index].select = true;
					this.seen = true;
				} else{
					todos2[index].select = false;
					this.seen = false;
				}
			}
		}
	}
</script>

<style>
	@import '../assets/css/help_center.css';
</style>


