<template>
	<div class="art-list">
		<div class="art-box" v-for="(todo1, index) in todos1" :class="[todo1.select ? activeClass : '']" v-on:click="tab2(todos1,index)">
			<div class="art-title">{{todo1.text}}</div>
			<div v-if="seen" v-html="todo1.text1" class="art-answ">{{todo1.text1}}</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'hello',
		data: function() {
			return {
				activeClass: 'select',
				seen: false,
				todos1: [{
						text: '',
						text1: '15178',
						select: false
					},
					{
						text: '',
						text1: '1<br/>2',
						select: false
					},
					{
						text: '',
						text1: 'APP',
						select: false
					},
					{
						text: '',
						text1: ' = /365',
						select: false
					},
					{
						text: '',
						text1: 'T+1',
						select: false
					},
					{
						text: '',
						text1: '',
						select: false
					},
					{
						text: '',
						text1: 'T+1',
						select: false
					},
					{
						text: '',
						text1: '',
						select: false
					},
					{
						text: '',
						text1: '<br/>1.<br/>2.<br/>3.<br/>4.<br/>5.<br/>',
						select: false
					}
				]
			}
		},
		methods: {
			tab2: function(todos1, index) {
				/**/
				todos1.forEach(function(el) {
					el.select = false;
				});
//				todos1[index].select = true;
				if (this.seen == false) {
					todos1[index].select = true;
					this.seen = true;
				} else{
					todos1[index].select = false;
					this.seen = false;
				}
			}
		}
	}
</script>

<style>
	@import '../assets/css/help_center.css';
</style>


